}
.asideBtn .icon {
  margin: 0 auto;
  background-image: url(../../static/img/sprite-common.db5c49a.png);
  background-repeat: no-repeat;
  background-size: 303px 287px;
  width: 32px;
  height: 21px;
  background-position: -160px -24px;
}
.logoDiv {
  height: 48px;
  width: 1200px;
  /*超出宽度也无所谓，自动居中*/
  display: table-cell;
  vertical-align: middle;
}
.logo {
  margin: 0 auto;
  width: auto;
  height: 48px;
  display: block;
}
.nav {
  color: #ccc;
  font-size: 0.14px;
  font-weight: 700;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 39px;
          flex: 0 0 39px;
}
.navBtn {
  padding: 11px 5px;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  /* height       : 38px; */
  background-color: #000;
  border-left: 1px solid #262626;
  border-bottom: 1px solid #262626;
  color: #ccc;
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
}
.main {
  position: relative;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 100%;
          flex: 0 1 100%;
  overflow: hidden;
}
.shadow {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 50;
  width: 100%;
  height: 100%;
}
.dn {
  display: none !important;
}
.leftMenu {
  width: 270px;
  min-height: 100%;
  z-index: 90;
  background-color: #111;
  border-top: 2px solid #000;
  position: absolute;
  top: 0px;
  left: -270px;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  overflow-x: hidden;
  overflow-y: auto;
}
.leftMenuShow {
  left: 0px;
  height: auto;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  overflow-x: hidden;
  overflow-y: auto;
}
.menuBtn,
.menuGap {
  width: 100%;
  height: 38px;
  line-height: 38px;
  border-bottom: 1px solid #111;
  color: #f90;
  display: block;
  padding: 9px 0 9px 17px;
  font-size: 0.9em;
  border-bottom: 2px solid #000;
}
.menuGap {
  height: 10px;
}
.alert,
.confirm {
  border-radius: 5px;
  border-color: #000;
  margin: auto;
  padding: 20px;
  z-index: 150;
  color: #f90;
  border: 2px solid #f90;
  background-color: #333;
}
.confirmContent {
  margin-bottom: 20px;
  text-align: center;
  vertical-align: middle;
}
.confirm {
  padding-bottom: 10px !important;
}
.c_ensure,
.c_cancel {
  margin: 0 10px;
  padding: 3 20px;
  font-size: 14px;
  border-radius: 4px;
  display: inline-block;
  border: 2px solid #f90;
  -webkit-box-shadow: none;
          box-shadow: none;
  line-height: 20px;
  white-space: nowrap;
  text-align: center;
  background-color: #2f2f2f;
  color: #acacac;
}
.navBtn.hover {
  color: #f90;
}
.leftMenu .blank {
  height: 48px;
  pointer-events: none;
  background-color: #000;
}




































